.App {
  padding: 0;
  margin: 0;
}

.tabIcon {
  width: 30px;
  height: 30px;
  border-radius: 4px;
  background: transparent;
}

.content-wrapper {
  margin: 0 15px;
}

.content-wrapper > h2 {
  display: inline-block;
  width: 100%;
  height: 40px;
  font-size: 3.2vw;
  margin: 13px;
  margin-left: 5vw;
  line-height: 40px;
  color: #3c91e8;
}

.content-wrapper .main {
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  gap: 20px;
}

.content-wrapper .main .left{
  flex: 3;
  /* height: calc(100vh - 40px); */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  gap: 15px;
}

.content-wrapper .main .right{
  flex: 2;
  /* height: calc(100vh - 40px); */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.content-wrapper .main .right > span{
  font-size: 2.5vw;
  color: #444;
  letter-spacing: 3px;
}

.content-wrapper .other {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content-wrapper .other video {
  width: 100%;
  /* height: 99vh; */
  margin-top: 30px;
}

.content-wrapper .main .left .bigImg{
  margin-left: 5%;
}

